import React from "react";
import { Link } from "gatsby"

// Styles
import "./HomeTestimonials.styles.scss";

// Components
import HomeTestimonialImage from "./HomeTestimonialImage.component";
import DividerArrow from "../DividerArrow/DividerArrow.component";

const HomeTestimonials = () => (
    <div className="home-testimonials">
        <DividerArrow />
        <h2>Testimonials</h2>
        <div className="home-testimonials-container">
            <HomeTestimonialImage />
            <div className="home-testimonials-content">
                <h3><em>Consulting veterinarian puts Tonisity Px to the test.</em></h3>
                <h4>Ryan Strobel, DVM.</h4>
                <p className="sub-head">Swine Vet Center, St. Peter, MN</p>
                <p>“We needed to stabilize production at a sow farm and were looking for ways to save pigs by lowering  pre-weaning mortality. Litters that received <strong><em>Tonisity Px</em></strong> clearly <strong><em>out-performed</em></strong> those that didn’t, by <strong><em>weaning more pigs</em></strong>, having <strong><em>lower mortality rates</em></strong>. This was a large comparison of nearly 1500 litters and 20,000 pigs, giving the producer <strong><em>high confidence</em></strong> in results.”</p>
                <Link to="/testimonials">Read More Testimonials</Link>
            </div>

        </div>
    </div>
)

export default HomeTestimonials;